import { _decorator, Component, Node, tween, UIOpacity } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Animation')
export class Animation extends Component {
  start() {}

  fadeIn(node: Node, duration: number, onComplete?: () => void) {
    let opacity = node.getComponent(UIOpacity);
    if (!opacity) {
      opacity = node.addComponent(UIOpacity); // Ensure UIOpacity is added
    }

    opacity.opacity = 0; // Start fully transparent

    // Fade to fully visible
    tween(opacity)
      .to(duration, { opacity: 255 }, { easing: 'linear' })
      .call(() => {
        if (onComplete) onComplete();
      })
      .start();
  }

  update(deltaTime: number) {}
}
